            <p> : 0</p>
            <p> : 0</p>
            <p> : 0</p>
            <p>: 0</p>
            <p> : 0</p>
            <v-divider />
            <v-row>
              <v-col
                cols="1"
              >
                <v-text-field
                  class="float-left"
                  outlined
                  label=""
                  max-width="200"
                />
              </v-col>
              <v-col
                cols="1"
              >
                <span class="ml-3 float-left"></span>
              </v-col>
              <v-col
                cols="1"
              >
                <v-select
                  :items="types"
                  class="ml-3"
                  outlined
                  max-width="50"
                />
              </v-col>
            </v-row>
            <v-row>
              <v-col
                cols="2"
              >
                <v-text-field
                  class="float-left"
                  outlined
                  label=" :"
                  max-width="200"
                />
              </v-col>
              <v-col
                cols="2"
              >
                <v-text-field
                  class="float-left"
                  outlined
                  label=":"
                  max-width="200"
                />
              </v-col>
            </v-row>
          </v-container>
          <v-row>
            <v-btn
              color="primary"
              @click="e1 = 5"
            >
              
            </v-btn>

            <v-btn
              text
              @click="e1 = 3"
            >
              
            </v-btn>
          </v-row>
        </v-stepper-content>
        <v-stepper-content
          step="5"
        >
          <v-container>
            <v-row>
              <v-col
                cols="6"
              >
                <v-row>
                  <v-select
                    v-model="task"
                    :items="tasks"
                    class="ml-3"
                    outlined
                    max-width="50"
                    label=" "
                  />
                </v-row>
                <v-row>
                  <v-text-field
                    v-model="whatToDo"
                    label=" ?"
                  />
                </v-row>
                <v-row>
                  <v-textarea
                    v-model="desc"
                    label=""
                  />
                </v-row>
              </v-col>
              <v-col
                cols="6"
              >
              </v-col>
            </v-row>
          </v-container>
          <v-row>
            <v-btn
              color="primary"
              @click="e1 = 6"
            >
              
            </v-btn>

            <v-btn
              @click="e1 = 4"
            >
              
            </v-btn>
          </v-row>
        </v-stepper-content>
        <v-stepper-content
          step="6"
        >
          <v-container>
            <v-row>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="$store.state.client.lastName"
                  label=":"
                  disabled
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="$store.state.client.firstName"
                  label=":"
                  disabled
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="$store.state.client.patronymic"
                  label=":"
                  disabled
                />
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <v-text-field
                  v-model="$store.state.start"
                  label="*:"
                  disabled
                  v-bind="attrs"
                  prepend-icon="mdi-calendar-blank-multiple"
                  v-on="on"
                />
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <v-text-field
                  v-model="$store.state.end"
                  label="*:"
                  disabled
                  v-bind="attrs"
                  prepend-icon="mdi-calendar-blank-multiple"
                  v-on="on"
                />
              </v-col>
            </v-row>
            <v-btn
              color="primary"
              @click="reserveValidate()"
              v-if="$store.state.status==='pre-reserve' || $store.state.status==='empty' || $store.state.status===''"
            >
              
            </v-btn>
            <v-btn
              color="primary"
              @click="bookingValidate()"
              v-if="$store.state.status==='reserve'|| $store.state.status==='pre-booking' || $store.state.status===''"
            >
              
            </v-btn>
            <v-btn
              color="primary"
              @click="setPopulate()"
              v-if="$store.state.status==='booking' || $store.state.status==='half-booking' || $store.state.status===''"
            >
              
            </v-btn>
            <v-btn
              color="primary"
              v-if="$store.state.status==='populate'"
              @click="$store.commit('UNSET_POPULATE')"
            >
              
            </v-btn>
            <v-btn
              color="info"
            >
              
            </v-btn>
            <v-btn
              color="danger"
              @click="annulate()"
            >
              
            </v-btn>
            <v-btn
              color="danger"
            >
              
            </v-btn>
            <v-btn
              color="danger"
            >
              
            </v-btn>
          </v-container>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>

    <v-snackbar
      v-model="$store.state.snackbar"
      :bottom="'bottom'"
      :color="'red'"
      :left="'left'"
      :timeout="6000"
    >
      {{ $store.state.text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          dark
          text
          v-bind="attrs"
          @click="$store.state.snackbar = false"
        >
          X
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
  // import Form from '../components/Form'
  // import store from '../../../store'
  import { mapMutations } from 'vuex'
  export default {
    components: {
      // eslint-disable-next-line vue/no-unused-components
      Form: () => import('../components/Form'),
    },
    data () {
      return {
        whatToDo: '',
        desc: '',
        task: '',
        tasks: [
          { text: '     ', value: 'task1' },
          { text: '    ', value: 'task2' },
          { text: '   ', value: 'task3' },
        ],
        types: ['%', '.'],
        booking: {
          reservSource: '',
          reservNumber: '',
          earlyIn: false,
          latelyOut: false,
          sleepingArea: false,
          sleepingAreaCount: [1, 2, 3, 4, 5, 6, 7, 8, 9],
          priceType: '',
          status: '',
          noteType: '',
        },
        noteType: [
          { text: ' ', value: 'undefined' },
          { text: '', value: 'accepted' },
          { text: '', value: 'cancelled' },
        ],
        status: [
          { text: ' ', value: 'undefined' },
          { text: '', value: 'accepted' },
          { text: '', value: 'canceled' },
          { text: '', value: 'out' },
          { text: '', value: 'in' },
          { text: '', value: 'live' },
          { text: '', value: 'reserve' },
          { text: 'Overbooking', value: 'overbooking' },
        ],
        priceType: [' ', ' '],
        rooms: [
          { text: 1, value: '1' },
          { text: 2, value: '2' },
          { text: 3, value: '3' },
          { text: 4, value: '4' },
          { text: 5, value: '5' },
          { text: 6, value: '6' },
          { text: 7, value: '7' },
          { text: 8, value: '8' },
          { text: 9, value: '9' },
          { text: 10, value: '10' },
        ],
        time: null,
        menu2: false,
        menu3: false,
        menu4: false,
        time2: null,
        menu: false,
        tab: null,
        step: 1,
        e1: 1,
        valid: true,
        firstname: '',
        lastname: '',
        nameRules: [
          v => !!v || 'Name is required',
          v => v.length <= 10 || 'Name must be less than 10 characters',
        ],
        email: '',
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+/.test(v) || 'E-mail must be valid',
        ],
        reservSource: [
          { text: '', value: 'Reception' },
          { text: '', value: 'Instagram' },
          { text: ' ', value: 'Target' },
          { text: ' ', value: 'Hostel' },
          { text: '', value: 'Vk' },
          { text: 'Site', value: 'Site' },
          { text: 'booking', value: 'booking' },
        ],
      }
    },
    methods: {
      ...mapMutations({
        setBookingData: 'SET_BOOKING_DATA',
        reserve: 'SET_RESERVE',
        setPopulate: 'SET_POPULATE',
        annulate: 'DELETE',
      }),
      reserveValidate () {
        if (!this.$refs.form.validate()) {
          this.e1 = 2
        } else {
          this.$store.commit('SET_RESERVE')
        }
      },
      bookingValidate () {
        if (!this.$refs.form.validate()) {
          this.e1 = 2
        } else {
          this.setBookingData()
        }
      },
    },
    watch: {
      task: function () {
        if (this.task === 'task1') {
          this.whatToDo = '    '
          this.desc = ''
        } else if (this.task === 'task2') {
          this.whatToDo = '   '
          this.desc = ''
        } else {
          this.whatToDo = '   '
          this.desc = ' :   \n' +
            ': \n' +
            '- : NaN\n' +
            ': 17-08-2020 13:00'
        }
      },
    },
    created () {
    },
  }
</script>
